*::before,
*::after {
  box-sizing: border-box;
}
* {
  margin: 0;
}
body {
  font-family: "Inter", sans-serif;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  background-color: var(--body-color);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-color);
  padding: 4rem;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
input,
button,
textarea,
select {
  font: inherit;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

:root {
  --container-color: #eaf5ff40;
  --body-color: #3c61c0;
  --text-color: white;
  --text--greyed-color: #d7c9c9;
}

.container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.weather-location {
  font-size: 2.5rem;
}

.weather-date-time {
  font-size: 2rem;
  color: var(--text--greyed-color);
}

.weather-container {
  position: relative;
  background-color: var(--container-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5rem;
  border-radius: 8px;
}

.weather-current {
  display: flex;
  align-items: center;
}

.current-temperature {
  font-size: 6rem;
  font-weight: 500;
}

.current-icon {
  width: 240px;
}

.weather-summary {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.summary-heading {
  color: #2527a0c3;
  font-size: 1.35rem;
}

.summary-feels-like {
  color: #2527a0c3;
  font-size: 1.35rem;
  font-weight: 600;
}

.summary-value {
  color: var(--text-color);
  font-size: 1.2rem;
  font-weight: 500;
}

.weather-details-container {
  background-color: var(--container-color);
  padding: 5rem;
  border-radius: 8px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  align-items: center;
  justify-content: center;
}

.weather-detail-box {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}

.weather-detail-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4rem;
  font-size: 2.5rem;
  color: var(--text-color);
}

.weather-detail-values {
  display: flex;
  flex-direction: column;
}

.weather-detail-heading {
  font-size: 1.5rem;
  color: #2527a0c3;
}

.weather-detail-value {
  font-size: 1.375rem;
  color: var(--text-color);
}

.search-container {
  margin: auto;
  display: flex;
  margin-top: 2rem;
}

.search-form {
  position: relative;
  width: 100%;
  max-width: 500px;
  background: #eaf5ff40;
  display: flex;
  border-radius: 60px;
  align-items: center;
  padding: 0.4em 0.6em;
  border: 1px solid transparent;
  transition: 0.5s;
}

.search-form:focus-within {
  border: 1px solid #fff;
}

.search-form input {
  background: transparent !important;
  border: 0;
  outline: none;
  padding: 0.2em;
  font-size: 1.1rem;
  color: white;
}

.search-form input::placeholder {
  color: rgba(255, 255, 255, 0.39);
}

.search-form button {
  border: 0;
  border-radius: 50px;
  width: 2.3rem;
  height: 2.3rem;
  background: #121365c3;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffffb8;
  cursor: pointer;
  transition: transform 0.5s;
}

.search-form button:hover {
  transform: scale(1.1);
}

#city-suggestions {
  max-height: 210px;
  overflow-y: auto;
  position: absolute;
  width: 100%;
  background-color: #121365;
  z-index: 10;
  top: 52px;
  left: 0;
  padding: 0.4em 0.6em;
  border-radius: 10px;
  display: none;
}

.suggestion {
  padding: 8px;
  cursor: pointer;
}

.suggestion:hover {
  background-color: #eaf5ff29;
}

/* Scrollbar width */
#city-suggestions::-webkit-scrollbar {
  width: 10px; /* Width of the scrollbar */
}

/* Track (the background of the scrollbar) */
#city-suggestions::-webkit-scrollbar-track {
  background: black; /* Color of the scrollbar track */
}

/* Handle (the draggable part of the scrollbar) */
#city-suggestions::-webkit-scrollbar-thumb {
  background-color: white; /* Color of the scrollbar handle */
}

/* Handle on hover */
#city-suggestions::-webkit-scrollbar-thumb:hover {
  background: black; /* Color of the scrollbar handle on hover */
}

/* The switch container */
.switch {
  position: absolute;
  display: inline-block;
  width: 60px;
  height: 34px;
  top: 20px;
  right: 20px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #121365c3;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
}

input:checked + .slider {
  background-color: #121365c3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #121365c3;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.slider-label-c {
  position: absolute;
  color: white;
  font-size: 14px;
  font-weight: bold;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
  left: 10px; /* Position C label on the left */
  user-select: none;
}

.slider-label-f {
  position: absolute;
  color: white;
  font-size: 14px;
  font-weight: bold;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
  right: 10px; /* Position F label on the right */
  user-select: none;
}

/* Toggle visibility of C and F */
input:checked + .slider .slider-label {
  opacity: 0.5;
}

input:checked + .slider .slider-label-f {
  opacity: 1;
}

.loader {
  width: 48px;
  height: 48px;
  border: 5px solid #FFF;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  }

  @keyframes rotation {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
  } `, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;;;EAGE,sBAAsB;AACxB;AACA;EACE,SAAS;AACX;AACA;EACE,gCAAgC;EAChC,gBAAgB;EAChB,mCAAmC;EACnC,mCAAmC;EACnC,iBAAiB;EACjB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,wBAAwB;EACxB,aAAa;AACf;;AAEA;;;;;EAKE,cAAc;EACd,eAAe;AACjB;AACA;;;;EAIE,aAAa;AACf;AACA;;;;;;;EAOE,yBAAyB;AAC3B;AACA;EACE,iBAAiB;AACnB;AACA;;;;;;EAME,kBAAkB;AACpB;;AAEA;EACE,4BAA4B;EAC5B,qBAAqB;EACrB,mBAAmB;EACnB,6BAA6B;AAC/B;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,eAAe;EACf,gCAAgC;AAClC;;AAEA;EACE,kBAAkB;EAClB,wCAAwC;EACxC,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,aAAa;EACb,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,QAAQ;AACV;;AAEA;EACE,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,wBAAwB;EACxB,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA;EACE,wCAAwC;EACxC,aAAa;EACb,kBAAkB;EAClB,aAAa;EACb,qCAAqC;EACrC,SAAS;EACT,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,2BAA2B;EAC3B,SAAS;AACX;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,WAAW;EACX,iBAAiB;EACjB,wBAAwB;AAC1B;;AAEA;EACE,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA;EACE,mBAAmB;EACnB,wBAAwB;AAC1B;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,gBAAgB;EAChB,qBAAqB;EACrB,aAAa;EACb,mBAAmB;EACnB,mBAAmB;EACnB,oBAAoB;EACpB,6BAA6B;EAC7B,gBAAgB;AAClB;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,kCAAkC;EAClC,SAAS;EACT,aAAa;EACb,cAAc;EACd,iBAAiB;EACjB,YAAY;AACd;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,SAAS;EACT,mBAAmB;EACnB,aAAa;EACb,cAAc;EACd,qBAAqB;EACrB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,gBAAgB;EAChB,eAAe;EACf,0BAA0B;AAC5B;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,kBAAkB;EAClB,WAAW;EACX,yBAAyB;EACzB,WAAW;EACX,SAAS;EACT,OAAO;EACP,oBAAoB;EACpB,mBAAmB;EACnB,aAAa;AACf;;AAEA;EACE,YAAY;EACZ,eAAe;AACjB;;AAEA;EACE,2BAA2B;AAC7B;;AAEA,oBAAoB;AACpB;EACE,WAAW,EAAE,2